.game-item {
  text-decoration: none;
  color: white;
  width: 320px;
  height: 320px;
  flex-shrink: 0;
  min-width: 156 px;
  background-color: rgb(36, 37, 41);
  border-radius: 15px;
  cursor: pointer;
  position: relative;
}

.game-item:not(:last-child) {
  margin-bottom: 30px;
  margin-right: 50px;
}

.game-item__title {
  font-size: 18px;
  font-weight: bold;
}

.game-item__genre {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.game-item__details {
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
}
.descr_block {
  height: 100%;
}
@media (max-width: 767px) {
  .game-item {
    width: 100%;
  }
}
